html {
    font-size: 62.5%;
}

.body {
    background-color: white;
    font-family: $secondaryFont;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

.container {
    max-width: 1140px;
    margin: 0 auto;
    padding: 0 2rem;
    width: 100%;
}

.page__title {
    padding: 1rem;
    font-size: 3.5rem;
    font-family: $primaryFont;
    font-weight: 600;
    color: $fontColor;
    text-transform: uppercase;
    text-align: center;

    @media screen and (max-width: 992px) {
        font-size: 3.2rem;
    }

    @media screen and (max-width: 767px) {
        font-size: 2.6rem;
    }
}

@import "../components/App/app.scss";
@import "../components/Header/header.scss";
@import "../components/Intro/intro.scss";
@import "../components/Slide/slide.scss";
@import "../components/Contacts/contacts.scss";
@import "../components/InfoBlock/info-block.scss";
@import "../components/Rules/rules.scss";
@import "../components/Gallery/gallery.scss";
@import "../components/Video/video.scss";
@import "../components/Footer/footer.scss";
